import { useEntityActionPopup, useNotistack, usePageChangeAfterEntityDelete } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { formRequest } from 'modules/entity/actions';
import { useDispatch } from 'react-redux';
import { CSVType } from '../types';

export function useCSVList() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<CSVType>();

	const { forwardToPreviousPage } = usePageChangeAfterEntityDelete({
		pageKey: 'page',
		entity: 'csv-list',
		entityName: 'AllCsvList'
	});

	const handleCsvDelete = (csv: CSVType) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'csv-list',
				name: 'AllCsvList',
				method: 'delete',
				url: `/${companyId}/my/company/imports/${csv.id}/`,
				deleteData: true,
				id: csv.id,
				cb: {
					success: () => {
						forwardToPreviousPage();
						showNotification({ message: 'Successfully deleted', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	const deleteCSVHandler = () => () => handleCsvDelete(deleteVisible!);

	return {
		deleteLoading,
		deleteCSVHandler,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible
	};
}
