import { useEntityActionPopup, useNotistack, useRouter } from 'shared/hooks';
import { useAppState } from 'shared/state';
import { formRequest } from 'modules/entity/actions';
import { useDispatch } from 'react-redux';
import { CSVViewType } from '../types';

export function useCsvView() {
	const { companyId } = useAppState();
	const dispatch = useDispatch();
	const { showNotification } = useNotistack();
	const { params } = useRouter();
	const {
		visible: deleteVisible,
		showVisible: showDeleteVisible,
		loading: deleteLoading,
		toggleLoading: toggleDeleteLoading,
		closePopup: closeDeletePopup
	} = useEntityActionPopup<CSVViewType>();

	const {
		visible: editVisible,
		showVisible: showEditVisible,
		closePopup: closeEditPopup
	} = useEntityActionPopup<CSVViewType>();

	const csvId = parseInt((params as { csvId: string })?.csvId);

	const handleClientDelete = (csvEmployee: CSVViewType) => {
		//show loader
		toggleDeleteLoading();
		dispatch(
			formRequest({
				entity: 'csvEmployees',
				name: `AllCsvEmployees`,
				method: 'delete',
				url: `/${companyId}/my/company/imports/${csvId}/data/${csvEmployee.id}/`,
				deleteData: true,
				id: csvEmployee.id,
				cb: {
					success: () => {
						showNotification({ message: 'Successfully deleted', variant: 'success' });
					},
					error: (err) => {
						showNotification({ message: 'Something went wrong', variant: 'error' });
					},
					finally: () => {
						toggleDeleteLoading();
						closeDeletePopup();
					}
				},
				params: {},
				primaryKey: 'id',
				values: {}
			})
		);
	};

	return {
		deleteLoading,
		handleClientDelete,
		showDeleteVisible,
		closeDeletePopup,
		deleteVisible,
		editVisible,
		showEditVisible,
		closeEditPopup
	};
}
