import React, { createContext, useReducer, useContext, useCallback } from 'react';

type ContextType = {
	searchQuery: string;
	setSearchQuery: (query: string | null) => void;
	isStaff: string;
	setIsStaff: (type: string) => void;
};

// eslint-disable-next-line @typescript-eslint/no-non-null-assertion
export const CSVManagementContext = createContext<ContextType>(null!);
type setSearchQuery = {
	type: 'SET_SEARCH_QUERY';
	payload: string | null;
};

type StateType = {
	searchQuery: string | null;
};

type Actions = setSearchQuery;

const reducer = (state: StateType, action: Actions): StateType => {
	switch (action.type) {
		case 'SET_SEARCH_QUERY': {
			return {
				...state,
				searchQuery: action.payload
			};
		}
		default:
			return state;
	}
};

export function CSVManagementContextProvider({
	children
}: React.PropsWithChildren<Record<string, unknown>>) {
	const [state, dispatch] = useReducer(reducer, { searchQuery: null });

	const setSearchQuery = useCallback((query: string | null) => {
		dispatch({ type: 'SET_SEARCH_QUERY', payload: query });
	}, []);

	// eslint-disable-next-line react/jsx-no-constructed-context-values
	const contextValues = {
		searchQuery: state.searchQuery,
		setSearchQuery
	} as ContextType;

	return (
		<CSVManagementContext.Provider value={contextValues}>{children}</CSVManagementContext.Provider>
	);
}

export const useCSVManagementContext = () => {
	const csvManagementContext = useContext(CSVManagementContext);

	if (!csvManagementContext)
		throw new Error('useCSVManagementContext must be used within the CSVManagementContextProvider');
	return csvManagementContext;
};
