/* eslint-disable react/jsx-no-target-blank */
/* eslint react/no-multi-comp:0, no-console:0, no-alert: 0 */
import React, { useState } from 'react';
import { Button } from '@material-ui/core';
import { ErrorBoundary } from 'shared/components';
import Upload from 'rc-upload';

import PublishIcon from '@material-ui/icons/Publish';
import { makeStyles } from '@material-ui/core/styles';

import Papa from 'papaparse';
import { HtmlParser } from 'shared/components';
import isEmpty from 'lodash/isEmpty';
import { LinearProgress } from '@material-ui/core';
import { csvFileValidator, csvTools, parseNAsyncValidate } from 'shared/services';
import { CsvExample } from './CsvExample';
import { useAppState } from 'shared/state';
import { useLoadAll, useNotistack } from 'shared/hooks';
import config from 'config';
import { useAuth } from 'modules/auth/hooks/useAuth';
import { WhiteModal } from 'shared/components/Elements';
import { BsDownload } from 'react-icons/bs';
import { FaRegHandPointer } from 'react-icons/fa';

const { employeeCSVConfig, clientCSVConfig } = csvTools;
const useStylesProgress = makeStyles((theme) => ({
	root: {
		width: '100%',
		'& > * + *': {
			marginTop: theme.spacing(2)
		}
	}
}));

type Props = {
	onSuccess: () => void;
	url: string;
	fromStaffManagement?: boolean;
};

function CsvUploadFormInner({ onSuccess, url, fromStaffManagement = false }: Props) {
	const { companyId } = useAppState();
	const [loadingImage, setLoadingImage] = useState(false);
	const [imageUrl, setImageUrl] = useState(null);
	const classesProgress = useStylesProgress();
	const [showModal, setShowModal] = useState(false);
	const [csvData, setCsvData] = useState('');
	const [csvPrimaryCheckError, setCsvPrimaryCheckError] = useState<null | string>(null);
	const { showNotification } = useNotistack();
	const { token } = useAuth();
	const { reloadEntityAll } = useLoadAll();

	console.log('url', url);

	const CSVConfig = url === 'import-clients' ? clientCSVConfig : employeeCSVConfig;
	const configHeaderLength = url === 'import-clients' ? 7 : 6;
	const moreTxt =
		'There are more than required columns in your csv file. Please remove redundant columns. Refer to example. Or download sample csv file and edit it accordingly.';
	const lessTxt =
		'There are missing columns in your csv file. Please add appropriate columns. Refer to example. Or download sample csv file and edit it accordingly.';
	const importType = url === 'import-clients' ? 'client' : 'staff';

	const toggleExamplePopup = () => setShowModal((prev) => !prev);

	const fileOnStart = (file: any) => {
		setImageUrl(file.name);
		setLoadingImage(true);

		if (file.name.slice(-3).toLowerCase() === 'csv') {
			return new Promise((resolve, reject) => {
				Papa.parse(file, {
					complete: (parsedOutput) => {
						const { data } = parsedOutput;

						//@ts-ignore
						const dataColumnLength = data[0].length;
						//Check if there required length of columns. If it passes than we validate fields.
						if (dataColumnLength === configHeaderLength) {
							// we do csv validation
							// CSVFileValidator(file, CSVConfig).then((csvData) => {
							csvFileValidator(file, CSVConfig).then((csvData) => {
								if (isEmpty(csvData.inValidMessages)) {
									//Before uploading the csv we must validate email through
									// backend whether users are already staff || client in the business
									csvData &&
										parseNAsyncValidate({
											csvData,
											companyId,
											resolve,
											reject,
											setCsvData,
											type: importType,
											file,
											setLoadingImage
										});
									setCsvData('');
								} else {
									const errors = csvData.inValidMessages
										.map((message: any) =>
											message.startsWith('<div') ? message : `<div class="red">${message}</div>`
										)
										.join('');
									setCsvData(errors);
									reject(new Error('Invalid csv file'));
									setLoadingImage(false);
								}
							});
						} else {
							if (dataColumnLength > configHeaderLength) {
								setCsvPrimaryCheckError(moreTxt);
							} else if (dataColumnLength < configHeaderLength) {
								setCsvPrimaryCheckError(lessTxt);
							}
							reject(new Error('Invalid csv file'));
							setLoadingImage(false);
						}
					}
				});
			});
		} else {
			setLoadingImage(false);
			showNotification({
				variant: 'error',
				message: 'Invalid file type. Download please sample of file csv'
			});
			return false;
		}
	};

	// eslint-disable-next-line no-unused-vars
	const fileOnSuccess = (file: any) => {
		setLoadingImage(false);
		setTimeout(() => {
			reloadEntityAll({
				url: `/${companyId}/my/company/imports/`,
				entity: 'csv-list',
				name: 'AllCsvList',
				params: {
					page: 1,
					limit: 10
				}
			});
			onSuccess();
			if (fromStaffManagement) {
				showNotification({
					variant: 'success',
					message: 'Please wait a while for csv file finishes processing.'
				});
			}
		}, 300);
	};

	return (
		<div className="case-form case-form_staff">
			<div className="flex-center-y mb-5">
				<button
					type="button"
					onClick={toggleExamplePopup}
					className="flex-center-y text-primary transition hover:text-primary-dark"
				>
					<span className="mr-2">Example</span>
					<FaRegHandPointer />
				</button>
				<span className="mx-2">|</span>
				<a
					href={`${
						url === 'import-clients'
							? 'https://djangoapp-konnex-crm.s3.us-east-2.amazonaws.com/files/kennekt_client.csv'
							: 'https://kennekt.com/files/kennekt_staff.csv'
					}`}
					download
					className="flex-center-y text-primary transition hover:text-primary-dark"
				>
					<span className="mr-2">Download CSV sample file</span>
					<BsDownload />
				</a>
			</div>
			<div className="mb-5">
				<div className="uploader-section">
					<div className="upload-file-styles-wrapper">
						<div className="upload-file-styles">
							<Upload
								accept=".csv"
								className="upload-file-styles"
								action={`${config.API_ROOT}/${companyId}/${url}/`}
								//@ts-ignore
								beforeUpload={fileOnStart}
								onSuccess={fileOnSuccess}
								showUploadList={false}
								headers={{
									Authorization: 'Token ' + token
								}}
							>
								<Button variant="outlined" aria-label="file-attach" className="mr-5">
									<PublishIcon /> Upload CSV file
								</Button>
							</Upload>
						</div>
						{imageUrl !== null ? (
							<div className="my-4">
								<p>{imageUrl}</p>
							</div>
						) : null}
					</div>
					{loadingImage ? (
						<div className={classesProgress.root}>
							<LinearProgress className="loading-file" />
						</div>
					) : null}
				</div>
			</div>
			{!isEmpty(csvData) && (
				<div className="mb-5 text-kred-pink">
					{/*@ts-ignore*/}
					<HtmlParser text={csvData} />
					<div className="red mt-5">
						<strong>Please correct and upload again</strong>
					</div>
				</div>
			)}
			{csvPrimaryCheckError && (
				<div className="mb-5">
					<div className="mt-5 text-sm text-kred-pink">
						<strong>{csvPrimaryCheckError}</strong>
					</div>
				</div>
			)}
			<WhiteModal handleModalOpen={toggleExamplePopup} open={showModal}>
				<CsvExample isClient={url === 'import-clients'} />
			</WhiteModal>
			<div className="case-form__submit-row">
				<Button
					className="transition dark:bg-primary-300 dark:text-white dark:hover:bg-primary-200"
					variant="contained"
					onClick={onSuccess}
				>
					Close
				</Button>
			</div>
		</div>
	);
}

export function CsvUploadForm(props: Props) {
	return (
		<ErrorBoundary>
			<CsvUploadFormInner {...props} />
		</ErrorBoundary>
	);
}
