import React from 'react';
import {
	Table,
	TableBody,
	TableCell,
	TableContainer,
	TableHead,
	TableRow
} from 'shared/components/Elements';

type Props = {
	isClient: boolean;
};
export function CsvExample({ isClient }: Props) {
	return (
		<TableContainer>
			<Table aria-label="simple table">
				<TableHead>
					<TableRow>
						{isClient && <TableCell>Client Business Name*</TableCell>}

						<TableCell>{`First Name(required)`}</TableCell>
						<TableCell>{`Last Name(required)`}</TableCell>
						<TableCell>{`Email(required)`}</TableCell>
						<TableCell>{`Phone(required)`}</TableCell>
						<TableCell>{`Role(required)`}</TableCell>
						<TableCell>Admin Privilege(required)</TableCell>
					</TableRow>
				</TableHead>
				<TableBody>
					<TableRow>
						{isClient && (
							<TableCell component="th" scope="row">
								Example Business Name
							</TableCell>
						)}

						<TableCell>John</TableCell>
						<TableCell>Doe</TableCell>
						<TableCell>john.doe@example.com</TableCell>
						<TableCell>1-541-754-3010</TableCell>
						<TableCell>admin | manager | member (please put only one of these)</TableCell>
						<TableCell>yes</TableCell>
					</TableRow>
				</TableBody>
			</Table>
		</TableContainer>
	);
}
