import React, { lazy, Suspense } from 'react';
import { StandardPageLayout } from '../../shared/layouts';
import { Helmet } from 'react-helmet';
import { SimpleBtn, WhiteModal } from '../../shared/components/Elements';
import { ErrorBoundary, Loader } from '../../shared/components';
import { Route, Switch } from 'react-router-dom';
import { useCsvManagement } from './hooks';
import { CSVManagementContextProvider } from './contexts';
import { CsvSearch, CsvUploadForm } from './components';
import { isMobile } from 'react-device-detect';
import { useAppState } from '../../shared/state';

const CSVList = lazy(() => import('./nestedPages/CSVList'));
const CSVDetails = lazy(() => import('./nestedPages/CSVDetails'));

export function Csv() {
	const { path, unreadCasesQuantity, isHidden, toggleCsvUpload, uploadLink, location } =
		useCsvManagement();
	const { isAdmin } = useAppState();

	return (
		<StandardPageLayout>
			<div className="flex min-h-full max-w-full flex-grow flex-col lg:p-4">
				{(!isHidden || unreadCasesQuantity === 0) && (
					<Helmet>
						<title>{'KENNEKT | CSV Management'}</title>
					</Helmet>
				)}
				<div className="mb-5 flex items-center justify-between">
					<div className="flex w-full flex-wrap items-center justify-between">
						<h2 className="mr-5 py-4 text-xl font-bold dark:text-white md:text-3xl">
							CSV Management
						</h2>
						<div className="flex w-full flex-col items-end sm:flex-row sm:items-center md:w-auto">
							<CsvSearch />
							{!location.pathname.includes('view') && (
								<div className="flex items-center justify-between">
									<SimpleBtn disabled={!isAdmin} onClick={() => toggleCsvUpload('import-employee')}>
										{!isMobile ? 'New' : null} Business CSV
									</SimpleBtn>
									<SimpleBtn disabled={!isAdmin} onClick={() => toggleCsvUpload('import-clients')}>
										{!isMobile ? 'New' : null} Client CSV
									</SimpleBtn>
								</div>
							)}
						</div>
					</div>
					<WhiteModal
						handleModalOpen={() => toggleCsvUpload('')}
						open={!!uploadLink}
						maxWidth={446}
					>
						<CsvUploadForm onSuccess={() => toggleCsvUpload('')} url={uploadLink} />
					</WhiteModal>
				</div>

				<div className="relative flex max-w-full flex-grow flex-col">
					<div className="relative flex-grow">
						<Suspense fallback={<Loader tiled />}>
							<Switch>
								<Route exact path={`${path}`} component={CSVList} />
								<Route exact path={`${path}/view/:csvId`} component={CSVDetails} />
							</Switch>
						</Suspense>
					</div>
				</div>
			</div>
		</StandardPageLayout>
	);
}

export function CsvManagement() {
	return (
		<ErrorBoundary>
			<CSVManagementContextProvider>
				<Csv />
			</CSVManagementContextProvider>
		</ErrorBoundary>
	);
}
