import { useState } from 'react';
import { useRouter, useUnreadCaseTab } from 'shared/hooks';

export function useCsvManagement() {
	const [uploadLink, setUploadLink] = useState('');

	const { match, location } = useRouter();
	const { url, path } = match;
	const { unreadCasesQuantity, isHidden } = useUnreadCaseTab('CSV Management');

	const toggleCsvUpload = (uploadLink = '') => {
		setUploadLink(uploadLink);
	};

	return {
		url,
		path,
		unreadCasesQuantity,
		isHidden,
		toggleCsvUpload,
		uploadLink,
		location
	};
}
